/* New CSS */

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto Slab", serif;
}

header, footer{
  font-family: "Raleway", sans-serif;
  text-align: center;
}

footer {
  margin-bottom: 2.5em;
}

h1,h3 {
  font-weight: 600;
  margin-bottom: 4px;
}
h1 {
  color: #f55d00;
}
h3 {
  color: #2c3e50;
}

.button {
  background-color: #f55d00;
  opacity: 0.6;
  transition: 0.3s;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.2em;
  font-weight: 800;
  text-shadow: 1px 1px 1px black;
  cursor: pointer;
}

.button:hover {opacity: 1}

.box {
  display: -ms-flexbox; /* IE10 */
  display: flex;
  -ms-flex-wrap: wrap; /* IE10 */
  flex-wrap: wrap;
  padding: 0 8px;
  margin-bottom: 2em;
}

/* Create four equal columns that sits next to each other */
.column {
  -ms-flex: 25%; /* IE10 */
  flex: 25%;
  max-width: 25%;
  padding: 0 16px;
}

.cifra {
  width: 100%;
  margin-top: 32px;
  padding: 2vw;
  color: #fff;
  min-height: 10em;
  display: flex;
  align-items: center;
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 1200px) {
  .column {
    -ms-flex: 50%;
    flex: 50%;
    max-width: 50%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
    -ms-flex: 100%;
    flex: 100%;
    max-width: 100%;
  }
}

